<template>
  <view class="privacy-wrapper">
    <view>
      <uni-title type="h1" align="center" color="#dd524d" title="蔓草兴趣隐私政策"></uni-title>
    </view>
    <view class="protocol-item">
      <text class="uni-text">
        本应用尊重并保护所有使用服务用户的个人隐私权。为了给您提供更准确、更有个性化的服务，本应用会按照本隐私权政策的规定使用和披露您的个人信息。但本应用将以高度的勤勉、审慎义务对待这些信息。除本隐私权政策另有规定外，在未征得您事先许可的情况下，本应用不会将这些信息对外披露或向第三方提供。本应用会不时更新本隐私权政策。
        您在同意本应用服务使用协议之时，即视为您已经同意本隐私权政策全部内容。本隐私权政策属于本应用服务使用协议不可分割的一部分。
      </text>
    </view>
    <view class="protocol-item">
      <uni-title type="h3" title="适用范围"></uni-title>
    </view>
    <view
      ><text>
        (a) 在您注册本应用帐号时，您根据本应用要求提供的个人注册信息；</text
      ></view
    >
    <view
      ><text
        >(b)
        在您使用本应用网络服务，或访问本应用平台网页时，本应用自动接收并记录的您的浏览器和计算机上的信息，包括但不限于您的IP地址、浏览器的类型、使用的语言、访问日期和时间、软硬件特征信息及您需求的网页记录等数据；</text
      ></view
    >
    <view
      ><text>(c) 本应用通过合法途径取得的信息数据。</text></view
    >
    <view>
      <text> 您了解并同意，以下信息不适用本隐私权政策： </text>
    </view>
    <view>
      <text> (a) 您在使用本应用平台提供的搜索服务时输入的关键字信息； </text>
    </view>
    <view>
      <text>
        (b)
        本应用收集到的您在本应用发布的有关信息数据，包括但不限于参与活动、成交信息及评价详情；
      </text>
    </view>
    <view>
      <text>
        (c) 违反法律规定或违反本应用规则行为及本应用已对您采取的措施。
      </text>
    </view>
    <view class="protocol-item">
      <uni-title type="h3" title="信息使用"></uni-title>
    </view>
    <view>
      <text>
        (a)本应用不会向任何无关第三方提供、出售、出租、分享或交易您的个人信息，除非事先得到您的许可，或该第三方和本应用（含本应用关联公司）单独或共同为您提供服务，且在该服务结束后，其将被禁止访问包括其以前能够访问的所有这些资料。
      </text>
    </view>
    <view>
      <text>
        (b)
        本应用亦不允许任何第三方以任何手段收集、编辑、出售或者无偿传播您的个人信息。任何本应用平台用户如从事上述活动，一经发现，本应用有权立即终止与该用户的服务协议。
      </text>
    </view>
    <view>
      <text>
        (c)
        为服务用户的目的，本应用可能通过使用您的个人信息，向您提供您感兴趣的信息，包括但不限于向您发出产品和服务信息，或者与本应用合作伙伴共享信息以便他们向您发送有关其产品和服务的信息（后者需要您的事先同意）。
      </text>
    </view>
    <view>
      <text>
        信息披露在如下情况下，本应用将依据您的个人意愿或法律的规定全部或部分的披露您的个人信息：
      </text>
    </view>
    <view>
      <text> (a) 经您事先同意，向第三方披露； </text>
    </view>
    <view>
      <text>
        (b)为提供您所要求的产品和服务，而必须和第三方分享您的个人信息；
      </text>
    </view>
    <view>
      <text>
        (c)
        根据法律的有关规定，或者行政或司法机构的要求，向第三方或者行政、司法机构披露；
      </text>
    </view>
    <view>
      <text>
        (d)
        如您出现违反中国有关法律、法规或者本应用服务协议或相关规则的情况，需要向第三方披露；
      </text>
    </view>
    <view>
      <text>
        (e)
        如您是适格的知识产权投诉人并已提起投诉，应被投诉人要求，向被投诉人披露，以便双方处理可能的权利纠纷；
      </text>
    </view>
    <view>
      <text> (f) 其它本应用根据法律、法规或者网站政策认为合适的披露。 </text>
    </view>
    <view>
      <text>
        信息存储和交换本应用收集的有关您的信息和资料将保存在本应用及（或）其关联公司的服务器上，这些信息和资料可能传送至您所在国家、地区或本应用收集信息和资料所在地的境外并在境外被访问、存储和展示。
      </text>
    </view>
    <view class="protocol-item">
      <uni-title type="h3" title="Cookie的使用"></uni-title>
    </view>
    <view>
      <text>
        (a)
        在您未拒绝接受cookies的情况下，本应用会在您的计算机上设定或取用cookies
        ，以便您能登录或使用依赖于cookies的本应用平台服务或功能。本应用使用cookies可为您提供更加周到的个性化服务，包括推广服务。
      </text>
    </view>
    <view>
      <text>
        (b)
        您有权选择接受或拒绝接受cookies。您可以通过修改浏览器设置的方式拒绝接受cookies。但如果您选择拒绝接受cookies，则您可能无法登录或使用依赖于cookies的本应用网络服务或功能。
      </text>
    </view>
    <view>
      <text> (c) 通过本应用所设cookies所取得的有关信息，将适用本政策。 </text>
    </view>
    <view class="protocol-item">
      <uni-title type="h3" title="信息安全"></uni-title>
    </view>
    <view>
      <text>
        (a)
        本应用帐号均有安全保护功能，请妥善保管您的用户信息。本应用不会将用户信息滥用和变造。尽管有前述安全措施，但同时也请您注意在信息网络上不存在“完善的安全措施”。
      </text>
    </view>
    <view>
      <text>
        (b)蔓草兴趣保留随时修改本政策的权利，因此请经常查看。如对本政策作出重大更改，蔓草兴趣会通过网站通知的形式告知。方披露自己的个人信息，如联络方式或者邮政地址。请您妥善保护自己的个人信息，仅在必要的情形下向他人提供。如您发现自己的个人信息泄密，尤其是本应用用户名及密码发生泄露，请您立即联络本应用所属开发者，以便本应用采取相应措施。</text
      >
    </view>
    <view class="protocol-item">
      <uni-title type="h3" title="本政策如何更新"></uni-title>
    </view>
    <view>
      <text> 如果蔓草兴趣的隐私政策变更，会将最新版隐私政策发布到这里。 </text>
    </view>
    <view class="protocol-item">
      <uni-title type="h3" title="如何联系蔓草兴趣所属者"></uni-title>
    </view>
    <view>
      <text>
        如您对本政策或其他相关事宜有疑问，请通过电话或邮箱与蔓草兴趣所属开发者联系。
      </text>
    </view>
    <view>
      <text> 联系电话：18018751313 </text>
    </view>
    <view>
      <text>
        邮&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;箱： Aiden_dai@aliyun.com
      </text>
    </view>
    <view class="protocol-item">
      <uni-title
        type="h3"
        title="注：《隐私政策》自2022年7月25日首次发布并生效实施。"
      ></uni-title>
    </view>
  </view>
